@mixin select-indicator($fill-bg: $select-indicator-color, $opacity: .54) {
  background: $select-bg $select-indicator no-repeat right $select-padding-x center;
  background-clip: padding-box;
  background-size: $select-bg-size;
}

// Form control focus state
//
// Generate a customized focus state and for any input with the specified color,
// which defaults to the `@input-border-color-focus` variable.
@mixin form-control-focus() {
  &:focus ~ &-bottom-line {
    background-size: 100% 100%, 100% 100%;
    transition-duration: 0.3s;
    border-color: $input-focus-border-color;
    outline: none;
  }
}
