// Textual form controls
//
// Replaces the browser default input with a Material Design styled one.

.tbmd-form-control {
  display: block;
  width: 100%;
  // height: $input-height;
  padding: $input-padding-y $input-padding-x 8px;
  border: none;
  // border-bottom: $input-border-width solid $input-border-color;
  border-radius: 0;
  @include transition($tbmd-input-transition);
  appearance: none;

  // Customize the `:focus` state.
  @include tbmd-form-control-focus();

  // Placeholder
  &::placeholder {
    color: $input-placeholder-color;
    @include transition($tbmd-input-placeholder-transition);
    // Override Firefox's unusual default opacity; see https://github.com/twbs/bootstrap/pull/11526.
    opacity: 1;
  }

  // ~ &-bottom-line {
  //   opacity: 1;
  // }


  &-bottom-line {
    // position: relative;
    // bottom: 1px;
    // left: 0;
    display: block;
    height: $input-focus-border-width;
    background: no-repeat center bottom, center calc(100% - 1px);
    background-size: 0 100%, 100% 100%;
    background-image: linear-gradient(to top, $input-focus-border-color $input-focus-border-width, fade-out($input-focus-border-color, 1) $input-focus-border-width), linear-gradient(to top, $input-border-color $input-border-width, fade-out($input-border-color, 1) $input-border-width);
    @include transition($tbmd-input-bottom-line-transition);
  }
}

select.tbmd-form-control {
  &:not([size]):not([multiple]) {
    height: $input-height;
    padding-right: $select-padding-right;
    cursor: pointer;
    @include tbmd-select-indicator;
    background-repeat: no-repeat;
    background-position: right center;
    background-clip: padding-box;
  }

  appearance: none;
}

.tbmd-form-control[multiple],
textarea.tbmd-form-control {
  border: 1px solid $input-border-color;

  &:focus {
    border-color: $input-focus-border-color;
  }
}


// Custom indicator
//
// Generates a shadow element to create our makeshift checkbox/radio background.

.tbmd-control-indicator {
  position: absolute;
  top: (($line-height-base - $tbmd-control-indicator-size) / 2);
  left: 0;
  display: block;
  width: $tbmd-control-indicator-size;
  height: $tbmd-control-indicator-size;
  border: $tbmd-control-indicator-border-radius solid $tbmd-control-indicator-border-color;
  border-radius: $tbmd-control-indicator-border-radius
}


