@mixin tbmd-select-indicator($fill-bg: $select-indicator-color, $opacity: .54) {
  background-image: str-replace(url(data:image/svg+xml,%3Csvg%20width%3D%2210px%22%20height%3D%225px%22%20viewBox%3D%227%2010%2010%205%22%20version%3D%221.1%22%20xmlns%3D%22http%3A%2F%2Fwww.w3.org%2F2000%2Fsvg%22%20xmlns%3Axlink%3D%22http%3A%2F%2Fwww.w3.org%2F1999%2Fxlink%22%3E%0A%20%20%20%20%3Cpolygon%20id%3D%22Shape%22%20stroke%3D%22none%22%20fill%3D%22%23#{$fill-bg}%22%20fill-rule%3D%22evenodd%22%20opacity%3D%22#{$opacity}%22%20points%3D%227%2010%2012%2015%2017%2010%22%3E%3C%2Fpolygon%3E%0A%3C%2Fsvg%3E), "#", "%23");
}

// Form control focus state
//
// Generate a customized focus state and for any input with the specified color,
// which defaults to the `@input-border-color-focus` variable.
@mixin tbmd-form-control-focus() {
  &:focus {
    border-color: $input-focus-border-color;
    outline: none;

    &::placeholder {
      color: $input-placeholder-focus-color
    }
  }
  &:focus ~ &-bottom-line {
    background-size: 100% 100%, 100% 100%;
    transition-duration: 0.3s;
  }
}
